/* Header Section*/
.Header {
    width: 100%;
    background-color: #fff;
    position: fixed;

    .header-div {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 0 0 20px;

        .main-browser {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            align-items: center;
            gap: 40px;
            padding: 40px;
        }
    }
}
.header-div{
    a{
        text-decoration: none;
    }

    h2 {
        font-size: 40px;
        color: #047857;
        font-weight: bold;
    }
}

.main-browser a {
    color: black;
    font-size: 20px;
    font-weight: bold;
    text-transform: capitalize;
    transition: all 0.5s;

    &:hover{
        color: #047857;
    }
}
