@import url("https://fonts.googleapis.com/css2?family=New+Amsterdam&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-style: normal;
}

img {
  width: 100%;
  margin: auto;
}

.link {
  text-decoration: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

/* Header Section*/
.Header {
  width: 100%;
  background-color: #fff;
  position: fixed;
}
.Header .header-div {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0 0 20px;
}
.Header .header-div .main-browser {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  gap: 40px;
  padding: 40px;
}

.header-div a {
  text-decoration: none;
}
.header-div h2 {
  font-size: 40px;
  color: #047857;
  font-weight: bold;
}

.main-browser a {
  color: black;
  font-size: 20px;
  font-weight: bold;
  text-transform: capitalize;
  transition: all 0.5s;
}
.main-browser a:hover {
  color: #047857;
}

/* Main Section */
.main-section {
  background-color: #047857;
  padding-top: 80px;
}

.main-section-div {
  display: flex;
  gap: 20px;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding-bottom: 80px;
  padding-top: 80px;
}
.main-section-div .main-img-div {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-section-div .main-img-div img {
  width: 60%;
  border-radius: 10px;
}

.main-title-div {
  width: 50%;
}
.main-title-div h1 {
  width: 80%;
  margin: 0 auto;
  color: #ffffff;
  font-size: 80px;
  font-family: "New Amsterdam", sans-serif;
  font-weight: 800;
  letter-spacing: 5px;
}

/* Login Section */
.login-section {
  width: 100%;
  background-color: #fff;
  padding: 40px;
}
.login-section .login-div {
  display: flex;
  flex-direction: row-reverse;
}

.login-img-div {
  width: 50%;
}

.login-form-div {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* Login form */
}
.login-form-div .login-form {
  display: flex;
  flex-direction: column;
  width: 65%;
  gap: 30px;
  background-color: #047857;
  padding: 30px;
  border-radius: 10px;
}
.login-form-div .login-form h2 {
  color: aliceblue;
  text-transform: uppercase;
  text-align: center;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 20px;
}

.login-il {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.login-il input {
  padding: 10px;
  color: gray;
}
.login-il label {
  color: aliceblue;
}

.btn-form {
  padding: 10px;
  border: none;
  font-weight: 700;
  text-transform: capitalize;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}
.btn-form:hover {
  background-color: #eb4325;
  color: aliceblue;
}

.login-img-div {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* About us */
.aboutus-section {
  /* background-color: #3b82f6; */
  background-color: #047857;
  padding: 40px;
}
.aboutus-section .aboutus-div {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 40px;
}

.aboutus-title {
  width: 50%;
}
.aboutus-title h2 {
  text-align: center;
  /* color: #6ee7b7; */
  /* color: #60a5fa; */
  color: #ffffff;
  font-size: 80px;
  font-family: "New Amsterdam", sans-serif;
  letter-spacing: 5px;
}

.aboutus-info {
  width: 50%;
}
.aboutus-info p {
  color: #fff;
  font-size: 20px;
  text-align: justify;
}

/* Footer Section */
.footer-div {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 40px;
}
.footer-div .footer-info {
  width: 50%;
}
.footer-div .footer-info p {
  font-weight: bold;
}

.footer-menu {
  width: 50%;
}

.main-browser {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 20px;
}
.main-browser a {
  color: black;
  font-size: 20px;
  font-weight: bold;
  text-transform: capitalize;
  transition: all 0.5s;
}
.main-browser a:hover {
  color: #047857;
}

.icon {
  font-size: 30px;
}