/* Footer Section */
.footer-div{
    display:flex ;
    flex-direction: row-reverse;
    width: 100%;
    justify-content: center;
    align-items: center;
    padding: 40px;

    .footer-info{
        width: 50%;
        p{
            font-weight: bold;
        }
    }
}
.footer-menu{
    width: 50%;
}

.main-browser{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 20px;

    a {
        color: black;
        font-size: 20px;
        font-weight: bold;
        text-transform: capitalize;
        transition: all 0.5s;

        &:hover {
            color: #047857;
        }
    }
}

.icon{
    font-size: 30px;
}