/* Main Section */
.main-section{
    background-color:#047857;
    padding-top: 80px;
}
.main-section-div {
    display: flex;
    gap: 20px;
    width: 100%;
    justify-content: center;
    align-items: center;
    padding-bottom: 80px;
    padding-top: 80px;

    
    .main-img-div {
        width: 50%;
        display: flex;
        align-items: center;
        justify-content: center;

        img{
            width: 60%;
            border-radius: 10px;
        }
    }
}

.main-title-div {
    width: 50%;

    h1 {
        width: 80%;
        margin: 0 auto;
        color: #ffffff;
        font-size: 80px;
        font-family: "New Amsterdam", sans-serif;
        font-weight: 800;
        letter-spacing: 5px;
    }
}




