
/* About us */
.aboutus-section{
    /* background-color: #3b82f6; */
    background-color: #047857;
    padding: 40px;

    .aboutus-div{
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        gap: 40px;
    }
}

.aboutus-title{
    width: 50%;

    h2 {
        text-align: center;
        /* color: #6ee7b7; */
        /* color: #60a5fa; */
        color: #ffffff;
        font-size: 80px;
        font-family: "New Amsterdam", sans-serif;
        letter-spacing: 5px;
    
    }
}

.aboutus-info{
    width: 50%;
    p{
        color:#fff;
        font-size: 20px;
        text-align: justify;
    }
}
