/* Login Section */
.login-section {
    width: 100%;
    background-color: #fff;
    padding: 40px;

    .login-div {
        display: flex;
        flex-direction: row-reverse;
    }
}

.login-img-div {
    width: 50%;
}

.login-form-div {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

    /* Login form */
    .login-form {
        display: flex;
        flex-direction: column;
        width: 65%;
        gap: 30px;
        background-color: #047857;
        padding: 30px;
        border-radius: 10px;
        h2 {
            color: aliceblue;
            text-transform: uppercase;
            text-align: center;
            font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
            font-size: 20px;
        }
    }
}


.login-il {
    display: flex;
    flex-direction: column;
    gap: 20px;

    input {
        padding: 10px;
        color: gray;
    }

    label {
        color: aliceblue;
    }
}


.btn-form {
    padding: 10px;
    border: none;
    font-weight: 700;
    text-transform: capitalize;
    transition: all 0.5s ease-in-out;
    cursor: pointer;

    &:hover {
        background-color: #eb4325;
        color: aliceblue;
    }
}

.login-img-div {
    display: flex;
    justify-content: center;
    align-items: center;
}